import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Skill from "./Skill";

import education from "./education";
import experience from "./experience";
import { Col } from "react-bootstrap";

function Resume() {
  return (
    // TODO: Change p tag for padding style
    <section id="resume" className="resume">
      <Container>
        <p />
        <Skill />
      </Container>
      <p />
      <Container>
        <div className="section-title">
          <h2>Resume</h2>
        </div>
        <Row xs={1} md={2} className="g-2">
          <Col>
            <h3 className="resume-title">Education</h3>
            <Container className="resume-item">
              {education.map(CreateCard)}
            </Container>
          </Col>
          <Col>
            <h3 className="resume-title">Professional Experience</h3>
            <Container className="resume-item">
              {experience.map(CreateCard)}
            </Container>
          </Col>
        </Row>
      </Container>
    </section>
  );
}

function CreateCard(props) {
  return (
    <Container key={props.id}>
      <Container>
        <h4 className="textTitle">{props.title}</h4>
        <h5 className="textYear">{props.year}</h5>
      </Container>
      <p>
        <em>{props.location}</em>
      </p>
      <ul>{props.description.map(mapDescription)}</ul>
    </Container>
  );
}

function mapDescription(props) {
  return <li key={props.id}>{props}</li>;
}

export default Resume;
