import React from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import PortfolioDetails from "./PortfolioDetails";

import projects from "./projects";

function PortfolioCards() {
  return (
    <section id="portfolio">
      <Container>
        <div className="section-title">
          <h2>Portfolio</h2>
          <p>
            Check out my portfolio on web, desktop, mobile and automation
            development
          </p>
        </div>
      </Container>
      <Row xs={1} md={3} lg={5} className="g-4 justify-content-center">
        {projects.map(createCard)}
      </Row>
    </section>
  );
}

function createCard(projects) {
  return (
    <PortfolioCard
      key={projects.id}
      title={projects.title}
      subtitle={projects.subtitle}
      description={projects.description}
      img={projects.img}
      category={projects.category}
      subcategory={projects.subcategory}
      techs={projects.techs}
    />
  );
}

// This is an example of using props
function PortfolioCard(props) {
  return (
    <Card className="PortfolioCard" style={{ width: "18rem" }}>
      <Card.Img variant="top" src={process.env.PUBLIC_URL + props.img} />
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <Card.Text>{props.subtitle}</Card.Text>
        {/* <Card.Text>{props.description}</Card.Text> */}
        {/* <Card.Text>{props.category}</Card.Text>
        <Card.Text>{props.subcategory}</Card.Text> */}
        <Button
          className="xsBtn"
          key={props.category}
          variant="info"
          size="sm"
          disabled
        >
          {props.category}
        </Button>
        {props.techs.map(SmallButton)}
        <p />
        <PortfolioDetails project={props} />
      </Card.Body>
    </Card>
  );
}

function SmallButton(element) {
  return (
    <Button
      className="xsBtn"
      key={element}
      variant="secondary"
      size="sm"
      disabled
    >
      {element}
    </Button>
  );
}

export default PortfolioCards;
