import React from "react";
import ProgressBar from "react-bootstrap/ProgressBar";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

import skills from "./skills";

function generateProgressBar(props) {
  return (
    <div key={props.id}>
      <p>{props.skill}</p>
      <ProgressBar
        animated
        now={props.percentage}
        label={`${props.percentage}%`}
      />
    </div>
  );
}

function Skill() {
  return (
    <section id="skills" className="skills section-bg">
      <Container>
        <div className="section-title">
          <h2>Skills</h2>
          <p>
            This is an overview of my skills. Some of them are used in this
            project and others can be seen in the portfolio
          </p>
        </div>
      </Container>
      <Container>
        <Row xs={1} md={2} className="g-2">
          {skills.map(generateProgressBar)}
        </Row>
      </Container>
    </section>
  );
}

export default Skill;
