import React from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Image from "react-bootstrap/Image";

function PortfolioDetails(props) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="primary" onClick={() => setModalShow(true)}>
        Details
      </Button>

      <PortfolioModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        key={props.project.id}
        title={props.project.title}
        subtitle={props.project.subtitle}
        description={props.project.description}
        img={props.project.img}
        category={props.project.category}
        subcategory={props.project.subcategory}
        techs={props.project.techs}
      />
    </>
  );
}

function PortfolioModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {props.title}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h4>{props.subtitle}</h4>
        <p>
          <Image
            src={process.env.PUBLIC_URL + props.img}
            className="detail-img"
            variant="top"
            alt={props.title}
            fluid
          />
        </p>
        <p>{props.description}</p>
        <p>
          Developed for {props.category} with {props.techs.join(", ")}
        </p>
        <p>{props.subcategory}</p>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

export default PortfolioDetails;
